// Import partials
@import 'variables';
@import 'nav';

:root {
    --max: #{$container-max-width};
}

body {
    margin: 0;
    font-family: $font-family-primary;
    background-color: $body-bg;
    color: $text-primary;
    line-height: $line-height-normal;
}

.container {
    max-width: $container-max-width;
    margin: 0 auto;
    padding: $container-padding;
}

.section {
    padding: $spacing-16 0;
}