.nav {
  background-color: #ffffff;
  border-bottom: 1px solid #e9ecef;
  padding: 1rem 0;
  position: relative;
  z-index: 1000;
}
.nav .container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 1.5rem;
}
.nav .row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0;
}
.nav__brand {
  flex: 0 0 auto;
}
.nav__content {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 1rem;
}
.nav__toggle {
  display: none;
  background: none;
  border: 1px solid #e9ecef;
  border-radius: 0.375rem;
  padding: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
}
.nav__toggle:hover {
  background-color: #f8f9fa;
}
.nav__toggle:focus {
  outline: 2px solid #007bff;
  outline-offset: 2px;
}
.nav__toggle--active .nav__toggle-icon {
  background-color: transparent;
}
.nav__toggle--active .nav__toggle-icon::before {
  transform: rotate(45deg);
  top: 0;
}
.nav__toggle--active .nav__toggle-icon::after {
  transform: rotate(-45deg);
  top: 0;
}
.nav__toggle-icon {
  display: block;
  width: 20px;
  height: 2px;
  background-color: #333;
  position: relative;
  transition: all 0.3s ease;
}
.nav__toggle-icon::before, .nav__toggle-icon::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #333;
  transition: all 0.3s ease;
}
.nav__toggle-icon::before {
  top: -6px;
}
.nav__toggle-icon::after {
  top: 6px;
}
.nav__menu {
  display: flex;
  align-items: center;
  gap: 1rem;
  width: 100%;
  justify-content: flex-end;
}
.nav__menu .navbar-collapse {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.nav__menu .navbar-nav {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin: 0;
  padding: 0;
  list-style: none;
}
.nav__menu .navbar-nav .nav-item {
  position: relative;
}
.nav__menu .navbar-nav .nav-item .nav-link {
  color: #333;
  text-decoration: none;
  font-weight: 500;
  padding: 0.5rem 0;
  transition: color 0.3s ease;
  position: relative;
}
.nav__menu .navbar-nav .nav-item .nav-link:hover {
  color: #007bff;
}
.nav__menu .navbar-nav .nav-item .nav-link.active {
  color: #007bff;
  font-weight: 600;
}
.nav__menu .navbar-nav .nav-item .nav-link.dropdown-toggle::after {
  content: "";
  display: inline-block;
  margin-left: 0.5rem;
  vertical-align: middle;
  border-top: 4px solid;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  transition: transform 0.3s ease;
}
.nav__menu .navbar-nav .nav-item .nav-link.dropdown-toggle[aria-expanded=true]::after {
  transform: rotate(180deg);
}
.nav__menu .navbar-nav .nav-item .dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #fff;
  border: 1px solid #e9ecef;
  border-radius: 4px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  min-width: 200px;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-10px);
  transition: all 0.3s ease;
  z-index: 1000;
}
.nav__menu .navbar-nav .nav-item .dropdown-menu .dropdown-item {
  display: block;
  padding: 0.75rem 1rem;
  color: #333;
  text-decoration: none;
  transition: background-color 0.3s ease;
}
.nav__menu .navbar-nav .nav-item .dropdown-menu .dropdown-item:hover {
  background-color: #f8f9fa;
  color: #007bff;
}
.nav__menu .navbar-nav .nav-item .dropdown-menu .dropdown-divider {
  height: 1px;
  background-color: #e9ecef;
  margin: 0.5rem 0;
}
.nav__menu .navbar-nav .nav-item .dropdown-menu .dropdown-header {
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
  color: #6c757d;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.nav__menu .navbar-nav .nav-item:hover .dropdown-menu {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.nav__cta {
  background-color: #000000;
  color: #ffffff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0.375rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
}
.nav__cta:hover {
  background-color: #0056b3;
  transform: translateY(-1px);
  box-shadow: 0 10px 15px -3px 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 0 1px 2px 0 rgba(0, 0, 0, 0.075), 0 4px 6px -2px 0 1px 2px 0 rgba(0, 0, 0, 0.075);
}
.nav__cta:active {
  transform: translateY(0);
}
.nav--mobile .nav__toggle {
  display: block;
}
.nav--mobile .nav__menu {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background-color: #fff;
  border-top: 1px solid #e9ecef;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  flex-direction: column;
  align-items: stretch;
  padding: 1rem;
  gap: 1rem;
}
.nav--mobile .nav__menu .navbar-collapse {
  flex-direction: column;
  align-items: stretch;
  width: 100%;
}
.nav--mobile .nav__menu .navbar-collapse.collapse:not(.show) {
  display: none;
}
.nav--mobile .nav__menu .navbar-nav {
  flex-direction: column;
  align-items: stretch;
  gap: 0;
}
.nav--mobile .nav__menu .navbar-nav .nav-item .nav-link {
  padding: 1rem;
  border-bottom: 1px solid #f8f9fa;
  display: block;
  width: 100%;
}
.nav--mobile .nav__menu .navbar-nav .nav-item .nav-link.dropdown-toggle::after {
  float: right;
  margin-top: 0.25rem;
}
.nav--mobile .nav__menu .navbar-nav .nav-item .dropdown-menu {
  position: static;
  opacity: 1;
  visibility: visible;
  transform: none;
  box-shadow: none;
  border: none;
  background-color: #f8f9fa;
  margin-top: 0;
  border-radius: 0;
}
.nav--mobile .nav__menu .navbar-nav .nav-item .dropdown-menu .dropdown-item {
  padding: 0.75rem 2rem;
  border-bottom: 1px solid #e9ecef;
}
.nav--mobile .nav__menu .navbar-nav .nav-item .dropdown-menu .dropdown-item:last-child {
  border-bottom: none;
}
.nav--mobile .nav__menu .nav__cta {
  width: 100%;
  margin-top: 1rem;
}

.logo__link {
  font-size: 1.5rem;
  font-weight: 700;
  color: #333333;
  text-decoration: none;
  transition: color 0.3s ease;
}
.logo__link:hover {
  color: #007bff;
}
.logo--small .logo__link {
  font-size: 1.25rem;
}
.logo--large .logo__link {
  font-size: 1.875rem;
}

@media (max-width: 768px) {
  .nav--mobile .nav__toggle {
    display: block;
  }
  .nav--mobile .nav__menu {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: #fff;
    border-top: 1px solid #e9ecef;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    flex-direction: column;
    align-items: stretch;
    padding: 1rem;
    gap: 1rem;
  }
}
@media (max-width: 992px) and (min-width: 769px) {
  .nav__menu .navbar-nav {
    gap: 1.5rem;
  }
}
:root {
  --max: 1100px;
}

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", system-ui, sans-serif;
  background-color: #ffffff;
  color: #333333;
  line-height: 1.5;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 1.5rem;
}

.section {
  padding: 4rem 0;
}