// Variables - Centralized Theme Configuration
// =========================================

// Colors
// ======

// Primary Colors
$primary-color: #007bff;
$primary-dark: #0056b3;
$primary-light: #66b3ff;

$color-1: #ccf7de;
$color-2: #f7f2cc;
$color-3: #ffd9ed;
$color-4: #ffffff;
$color-5: #000000;
$color-6: #fff1eb;

// Secondary Colors
$secondary-color: #6c757d;
$secondary-dark: #495057;
$secondary-light: #adb5bd;

// Neutral Colors
$white: #ffffff;
$black: #000000;
$gray-100: #f8f9fa;
$gray-200: #e9ecef;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #6c757d;
$gray-700: #495057;
$gray-800: #343a40;
$gray-900: #212529;

// Semantic Colors
$success: #28a745;
$info: #17a2b8;
$warning: #ffc107;
$danger: #dc3545;
$light: #f8f9fa;
$dark: #343a40;

// Background Colors
$body-bg: #ffffff;
$nav-bg: #ffffff;
$footer-bg: #f8f9fa;

// Text Colors
$text-primary: #333333;
$text-secondary: #6c757d;
$text-muted: #adb5bd;
$text-light: #ffffff;

// Link Colors
$link-color: $primary-color;
$link-hover-color: $primary-dark;
$link-visited-color: $primary-color;

// Border Colors
$border-color: #e9ecef;
$border-light: #f8f9fa;
$border-dark: #dee2e6;

// Shadow Colors
$shadow-sm: rgba(0, 0, 0, 0.075);
$shadow: rgba(0, 0, 0, 0.1);
$shadow-lg: rgba(0, 0, 0, 0.15);

// Typography
// ==========

// Font Families
$font-family-primary: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', system-ui, sans-serif;
$font-family-secondary: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', system-ui, sans-serif;
$font-family-mono: 'Fira Code', 'Monaco', 'Cascadia Code', 'Roboto Mono', monospace;

// Font Sizes
$font-size-xs: 0.75rem; // 12px
$font-size-sm: 0.875rem; // 14px
$font-size-base: 1rem; // 16px
$font-size-lg: 1.125rem; // 18px
$font-size-xl: 1.25rem; // 20px
$font-size-2xl: 1.5rem; // 24px
$font-size-3xl: 1.875rem; // 30px
$font-size-4xl: 2.25rem; // 36px
$font-size-5xl: 3rem; // 48px

// Font Weights
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-medium: 500;
$font-weight-semibold: 600;
$font-weight-bold: 700;
$font-weight-extrabold: 800;
$font-weight-black: 900;

// Line Heights
$line-height-tight: 1.25;
$line-height-snug: 1.375;
$line-height-normal: 1.5;
$line-height-relaxed: 1.625;
$line-height-loose: 2;

// Letter Spacing
$letter-spacing-tighter: -0.05em;
$letter-spacing-tight: -0.025em;
$letter-spacing-normal: 0;
$letter-spacing-wide: 0.025em;
$letter-spacing-wider: 0.05em;
$letter-spacing-widest: 0.1em;

// Spacing
// =======

// Base spacing unit
$spacing-unit: 1rem; // 16px

// Spacing scale
$spacing-0: 0;
$spacing-1: $spacing-unit * 0.25; // 4px
$spacing-2: $spacing-unit * 0.5; // 8px
$spacing-3: $spacing-unit * 0.75; // 12px
$spacing-4: $spacing-unit; // 16px
$spacing-5: $spacing-unit * 1.25; // 20px
$spacing-6: $spacing-unit * 1.5; // 24px
$spacing-8: $spacing-unit * 2; // 32px
$spacing-10: $spacing-unit * 2.5; // 40px
$spacing-12: $spacing-unit * 3; // 48px
$spacing-16: $spacing-unit * 4; // 64px
$spacing-20: $spacing-unit * 5; // 80px
$spacing-24: $spacing-unit * 6; // 96px
$spacing-32: $spacing-unit * 8; // 128px

// Layout
// ======

// Container
$container-max-width: 1100px;
$container-padding: $spacing-6;

// Breakpoints
$breakpoint-xs: 0;
$breakpoint-sm: 576px;
$breakpoint-md: 768px;
$breakpoint-lg: 992px;
$breakpoint-xl: 1200px;
$breakpoint-2xl: 1400px;

// Z-Index Scale
$z-index-dropdown: 1000;
$z-index-sticky: 1020;
$z-index-fixed: 1030;
$z-index-modal-backdrop: 1040;
$z-index-modal: 1050;
$z-index-popover: 1060;
$z-index-tooltip: 1070;

// Border Radius
$border-radius-sm: 0.25rem; // 4px
$border-radius: 0.375rem; // 6px
$border-radius-md: 0.5rem; // 8px
$border-radius-lg: 0.75rem; // 12px
$border-radius-xl: 1rem; // 16px
$border-radius-2xl: 1.5rem; // 24px
$border-radius-full: 9999px;

// Transitions
$transition-fast: 0.15s ease;
$transition-normal: 0.3s ease;
$transition-slow: 0.5s ease;

// Shadows
$shadow-sm: 0 1px 2px 0 $shadow-sm;
$shadow: 0 1px 3px 0 $shadow, 0 1px 2px 0 $shadow-sm;
$shadow-md: 0 4px 6px -1px $shadow, 0 2px 4px -1px $shadow-sm;
$shadow-lg: 0 10px 15px -3px $shadow, 0 4px 6px -2px $shadow-sm;
$shadow-xl: 0 20px 25px -5px $shadow, 0 10px 10px -5px $shadow-sm;
$shadow-2xl: 0 25px 50px -12px $shadow;

// Component Specific Variables
// ============================

// Navigation
$nav-height: 70px;
$nav-padding-y: $spacing-4;
$nav-padding-x: $spacing-6;
$nav-bg: $white;
$nav-border-color: $border-color;
$nav-link-color: $text-primary;
$nav-link-hover-color: $primary-color;
$nav-link-padding: $spacing-2 $spacing-4;

// Buttons
$btn-padding-y: $spacing-2;
$btn-padding-x: $spacing-4;
$btn-font-size: $font-size-base;
$btn-line-height: $line-height-normal;
$btn-border-radius: $border-radius;
$btn-font-weight: $font-weight-medium;

// Forms
$input-padding-y: $spacing-2;
$input-padding-x: $spacing-3;
$input-font-size: $font-size-base;
$input-line-height: $line-height-normal;
$input-border-color: $border-color;
$input-border-radius: $border-radius;
$input-focus-border-color: $primary-color;

// Cards
$card-padding: $spacing-6;
$card-border-radius: $border-radius-lg;
$card-border-color: $border-color;
$card-bg: $white;
$card-shadow: $shadow;

// Logo
$logo-font-size: $font-size-2xl;
$logo-font-weight: $font-weight-bold;
$logo-color: $text-primary;
$logo-hover-color: $primary-color;