// Navigation Component - BEM Methodology
// ======================================

.nav {
    // Block: .nav
    background-color: $nav-bg;
    border-bottom: 1px solid $nav-border-color;
    padding: $nav-padding-y 0;
    position: relative;
    z-index: $z-index-dropdown;

    // Bootstrap container and row styling
    .container {
        max-width: $container-max-width;
        margin: 0 auto;
        padding: 0 $container-padding;
    }

    .row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0;
    }

    // Element: .nav__brand
    &__brand {
        flex: 0 0 auto;
    }

    // Element: .nav__content
    &__content {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 1rem;
    }

    // Element: .nav__toggle
    &__toggle {
        display: none;
        background: none;
        border: 1px solid $border-color;
        border-radius: $border-radius;
        padding: $spacing-2;
        cursor: pointer;
        transition: all $transition-normal;

        &:hover {
            background-color: $gray-100;
        }

        &:focus {
            outline: 2px solid $primary-color;
            outline-offset: 2px;
        }

        // Modifier: .nav__toggle--active
        &--active {
            .nav__toggle-icon {
                background-color: transparent;

                &::before {
                    transform: rotate(45deg);
                    top: 0;
                }

                &::after {
                    transform: rotate(-45deg);
                    top: 0;
                }
            }
        }
    }

    // Element: .nav__toggle-icon
    &__toggle-icon {
        display: block;
        width: 20px;
        height: 2px;
        background-color: #333;
        position: relative;
        transition: all 0.3s ease;

        &::before,
        &::after {
            content: '';
            position: absolute;
            width: 100%;
            height: 2px;
            background-color: #333;
            transition: all 0.3s ease;
        }

        &::before {
            top: -6px;
        }

        &::after {
            top: 6px;
        }
    }

    // Element: .nav__menu
    &__menu {
        display: flex;
        align-items: center;
        gap: 1rem;
        width: 100%;
        justify-content: flex-end;

        // Bootstrap Navigation Classes (keeping for compatibility)
        .navbar-collapse {
            display: flex;
            align-items: center;
            gap: 1rem;
        }

        .navbar-nav {
            display: flex;
            align-items: center;
            gap: 2rem;
            margin: 0;
            padding: 0;
            list-style: none;

            .nav-item {
                position: relative;

                .nav-link {
                    color: #333;
                    text-decoration: none;
                    font-weight: 500;
                    padding: 0.5rem 0;
                    transition: color 0.3s ease;
                    position: relative;

                    &:hover {
                        color: #007bff;
                    }

                    // Active state
                    &.active {
                        color: #007bff;
                        font-weight: 600;
                    }

                    // Dropdown toggle
                    &.dropdown-toggle {
                        &::after {
                            content: '';
                            display: inline-block;
                            margin-left: 0.5rem;
                            vertical-align: middle;
                            border-top: 4px solid;
                            border-right: 4px solid transparent;
                            border-left: 4px solid transparent;
                            transition: transform 0.3s ease;
                        }

                        &[aria-expanded="true"]::after {
                            transform: rotate(180deg);
                        }
                    }
                }

                // Dropdown Menu
                .dropdown-menu {
                    position: absolute;
                    top: 100%;
                    left: 0;
                    background-color: #fff;
                    border: 1px solid #e9ecef;
                    border-radius: 4px;
                    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
                    min-width: 200px;
                    opacity: 0;
                    visibility: hidden;
                    transform: translateY(-10px);
                    transition: all 0.3s ease;
                    z-index: 1000;

                    .dropdown-item {
                        display: block;
                        padding: 0.75rem 1rem;
                        color: #333;
                        text-decoration: none;
                        transition: background-color 0.3s ease;

                        &:hover {
                            background-color: #f8f9fa;
                            color: #007bff;
                        }
                    }

                    .dropdown-divider {
                        height: 1px;
                        background-color: #e9ecef;
                        margin: 0.5rem 0;
                    }

                    .dropdown-header {
                        padding: 0.5rem 1rem;
                        font-size: 0.875rem;
                        color: #6c757d;
                        font-weight: 600;
                        text-transform: uppercase;
                        letter-spacing: 0.05em;
                    }
                }

                // Show dropdown on hover
                &:hover .dropdown-menu {
                    opacity: 1;
                    visibility: visible;
                    transform: translateY(0);
                }
            }
        }
    }

    // Element: .nav__cta
    &__cta {
        background-color: $color-5;
        color: $white;
        border: none;
        padding: $btn-padding-y $btn-padding-x;
        border-radius: $btn-border-radius;
        font-weight: $btn-font-weight;
        cursor: pointer;
        transition: all $transition-normal;
        white-space: nowrap;

        &:hover {
            background-color: $primary-dark;
            transform: translateY(-1px);
            box-shadow: $shadow-lg;
        }

        &:active {
            transform: translateY(0);
        }
    }

    // Modifier: .nav--mobile
    &--mobile {
        .nav__toggle {
            display: block;
        }

        .nav__menu {
            position: absolute;
            top: 100%;
            left: 0;
            right: 0;
            background-color: #fff;
            border-top: 1px solid #e9ecef;
            box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
            flex-direction: column;
            align-items: stretch;
            padding: 1rem;
            gap: 1rem;

            .navbar-collapse {
                flex-direction: column;
                align-items: stretch;
                width: 100%;

                &.collapse:not(.show) {
                    display: none;
                }
            }

            .navbar-nav {
                flex-direction: column;
                align-items: stretch;
                gap: 0;

                .nav-item {
                    .nav-link {
                        padding: 1rem;
                        border-bottom: 1px solid #f8f9fa;
                        display: block;
                        width: 100%;

                        &.dropdown-toggle::after {
                            float: right;
                            margin-top: 0.25rem;
                        }
                    }

                    .dropdown-menu {
                        position: static;
                        opacity: 1;
                        visibility: visible;
                        transform: none;
                        box-shadow: none;
                        border: none;
                        background-color: #f8f9fa;
                        margin-top: 0;
                        border-radius: 0;

                        .dropdown-item {
                            padding: 0.75rem 2rem;
                            border-bottom: 1px solid #e9ecef;

                            &:last-child {
                                border-bottom: none;
                            }
                        }
                    }
                }
            }

            .nav__cta {
                width: 100%;
                margin-top: 1rem;
            }
        }
    }
}

// Logo Component - BEM
// ====================

.logo {

    // Block: .logo
    &__link {
        font-size: $logo-font-size;
        font-weight: $logo-font-weight;
        color: $logo-color;
        text-decoration: none;
        transition: color $transition-normal;

        &:hover {
            color: $logo-hover-color;
        }
    }

    // Modifier: .logo--small
    &--small {
        .logo__link {
            font-size: $font-size-xl;
        }
    }

    // Modifier: .logo--large
    &--large {
        .logo__link {
            font-size: $font-size-3xl;
        }
    }
}

// Responsive Breakpoints
// ======================

@media (max-width: 768px) {
    .nav {
        &--mobile {
            .nav__toggle {
                display: block;
            }

            .nav__menu {
                position: absolute;
                top: 100%;
                left: 0;
                right: 0;
                background-color: #fff;
                border-top: 1px solid #e9ecef;
                box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
                flex-direction: column;
                align-items: stretch;
                padding: 1rem;
                gap: 1rem;
            }
        }
    }
}

@media (max-width: 992px) and (min-width: 769px) {
    .nav {
        &__menu {
            .navbar-nav {
                gap: 1.5rem;
            }
        }
    }
}